import { mapState } from 'vuex'
export default {
  components: {
    SinglePost: () => import('@/components/blog/posts/SinglePost'),
    PostComments: () => import('@/components/blog/comments/PostComments'),
    PostLoader: () => import('@/components/blog/posts/PostLoader')
  },
  computed: {
    ...mapState(['post'])
  },
  watch: {
    $route() {
      this.$store.dispatch('post/fetchPost', {
        slug: this.$route.params.slug,
        axios: this.$axios,
        updateView: true
      })
    }
  },
  async created() {
    await this.$store.dispatch('post/fetchPost', {
      slug: this.$route.params.slug,
      axios: this.$axios,
      updateView: true
    })
  },
  head() {
    return {
      title: this.post.post ? this.post.post.title : '',
      meta: [
        {
          name: 'keywords',
          content: this.post.post ? this.post.post.keywords : ''
        },
        {
          name: 'description',
          content: this.post.post ? this.post.post.description : ''
        }
      ]
    }
  }
}
